import {Component, Input, ChangeDetectionStrategy, Output, EventEmitter} from '@angular/core';
import {
   animate, state, style, transition, trigger
} from '@angular/animations';
import {VsanUiUtils} from "@util/vsan-util";

export enum PaneType {
   LEFT = 'left',
   RIGHT = 'right'
}

/*
   Animated slide panel component.
   We can safely enable OnPush change detection strategy since there is no other way to update
    the componentâ€™s UI rather than changing the value of the input property.
  */
@Component({
  selector: 'vsan-slide-panel',
  styleUrls: [ './slide-panel.scss' ],
  templateUrl: './slide-panel.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  animations: [
    trigger('slide-right', [
      state(PaneType.LEFT, style({
        transform: 'translateX(0)',
        opacity: '0'
      })),
      state(PaneType.RIGHT, style({
        transform: 'translateX(-50%)',
        opacity: '1'
      })),
      transition('* => *', animate(300))
    ]),
    trigger('slide-left', [
      state(PaneType.LEFT, style({
        right: '0'
      })),
      state(PaneType.RIGHT, style({
        right: '{{paneSize}}%'
      }), {params: {paneSize: 30}}),
      transition('* => *', animate(300))
    ])]
})
export class SlidePanelComponent {
   @Input()
   activePane: PaneType = PaneType.LEFT;

   @Output()
   activePaneChange: EventEmitter<PaneType> = new EventEmitter<PaneType>();

   private _detailsPaneSize: number = 30;

   /** The percentage width of the main content, to which the slide panel will expand. Throws exception if try to set
    * to value below 0 or above 100*/
   @Input()
   set detailsPaneSize(value: number) {
      // value is mandatory, validate it
      if (!VsanUiUtils.isNumericValue(value) || value > 100 || value < 0) {
         throw new Error("Value is mandatory and should be between 0 and 100.");
      }
      this._detailsPaneSize = value;
   }

   get detailsPaneSize(): number {
      return this._detailsPaneSize;
   }

   PaneType = PaneType;
   closeDetailsPane() {
      this.activePane = PaneType.LEFT;
      this.activePaneChange.emit(this.activePane);
   }
}


